<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div class="clearfix mb-3">
      <b-button class="float-right btn-info" left @click="$bvModal.show('modal');editMode=false;">Crear Entidad</b-button>
    </div>
    <div class="row">
      <div class="col-12">
        <div class="card" >
          <div class="card-body" >
            <h4 class="card-title"></h4>
            <div class="row mt-4">
              <div class="col-sm-12 col-md-6">
                <div id="tickets-table_length" class="dataTables_length">
                  <label class="d-inline-flex align-items-center">
                    Show&nbsp;
                    <b-form-select v-model="perPage" size="sm" :options="pageOptions"></b-form-select>&nbsp;entries
                  </label>
                </div>
              </div>
              <!-- Search -->
              <div class="col-sm-12 col-md-6">
                <div id="tickets-table_filter" class="dataTables_filter text-md-right">
                  <label class="d-inline-flex align-items-center">
                    Search:
                    <b-form-input
                      v-model="filter"
                      type="search"
                      placeholder="Search..."
                      class="form-control form-control-sm ml-2"
                    ></b-form-input>
                  </label>
                </div>
              </div>
              <!-- End search -->
            </div>
            <!-- Table -->
            <div class="table-responsive mb-0" style="min-height:500px">
              <b-table
                :items="entidades"
                :fields="fields"
                responsive="sm"
                :per-page="perPage"
                :current-page="currentPage"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                :filter="filter"
                :filter-included-fields="filterOn"
                @filtered="onFiltered"
              >
                <template v-slot:cell(actions)="data">

                <b-dropdown size="sm" class="">
                  <template v-slot:button-content>
                    Action
                    <i class="mdi mdi-chevron-down"></i>
                  </template>
                    <b-dropdown-item-button @click="editMode=true;ver=false;setear(data.item.id)"> Editar </b-dropdown-item-button>
                        <b-dropdown-item-button @click="eliminarEntidad(data.item.id)"> Eliminar </b-dropdown-item-button>
                        <b-dropdown-item-button @click="setPermisos(data.item.id)"> Permisos a administradores</b-dropdown-item-button>
                        <b-dropdown-item-button @click="setPermisos_coordinadores(data.item.id)"> Permisos a coordinadores</b-dropdown-item-button>
                        <b-dropdown-item-button @click="setPermisos_analistas(data.item.id)"> Permisos a analistas</b-dropdown-item-button>
                        <b-dropdown-item-button @click="editMode=false;ver=true;setear(data.item.id)"> Ver </b-dropdown-item-button>
                </b-dropdown>
                </template>
              </b-table>
            </div>
            <div class="row">
              <div class="col">
                <div class="dataTables_paginate paging_simple_numbers float-right">
                  <ul class="pagination pagination-rounded mb-0">
                    <!-- pagination -->
                    <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage"></b-pagination>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>


   <b-modal id="permisos_administradores" false size="lg"   title="Permisos a administradores" hide-footer>
      <h4>Administradores</h4>
        <b-row>
          <b-col>
                <v-select v-model="administrador_id" :options="admin" :reduce="admin => admin.id" :getOptionLabel="option => option.nombre+' '+option.apellido" :disabled="ver"></v-select>
          </b-col>
          <b-col>
            <button class="btn btn-success btn-sm" @click="asignarPermiso(administrador_id)" :disabled="!administrador_id">Asignar</button>
          </b-col>
        </b-row> 
      <hr>
      <h4>Permisos Otorgados</h4>
      <b-row>
        <div class="col-4" v-for="permisos in permisos_admin" :key="permisos.id">
          <div class="card mb-3">
            <div class="row g-0">
              <div class="col-md-4">
                <img src="" class="img-fluid rounded-start" alt="">
              </div>
              <div class="col-md-8">
                <div class="card-body">
                  <h5 class="card-title"> {{permisos.user.nombre}} {{permisos.user.apellido}}</h5>
                 <p class="card-text"><small class="text-muted">{{permisos.user.email}}</small></p>
                  <button class="btn btn-danger" @click="eliminarPermiso(permisos.id)"> Eliminar</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </b-row>
    </b-modal>

    <b-modal id="permisos_coordinadores" false size="lg"   title="Permisos a coordinadores" hide-footer>
      <h4>Coordinadores</h4>
        <b-row>
          <b-col>
                <v-select v-model="coordinador_id" :options="users" :reduce="users => users.id" :getOptionLabel="option => option.nombre+' '+option.apellido" :disabled="ver"></v-select>
          </b-col>
          <b-col>
            <button class="btn btn-success btn-sm" @click="asignarPermiso_coordinador(coordinador_id)" :disabled="!coordinador_id">Asignar</button>
          </b-col>
        </b-row> 
      <hr>
      <h4>Permisos Otorgados</h4>
      <b-row>
        <div class="col-4" v-for="permisos in permisos_coordinadores" :key="permisos.id">
          <div class="card mb-3">
            <div class="row g-0">
              <div class="col-md-4">
                <img src="" class="img-fluid rounded-start" alt="">
              </div>
              
              <div class="col-md-8">
                <div class="card-body">
                  <h5 class="card-title"> {{permisos.user.nombre}} {{permisos.user.apellido}}</h5>
                 <p class="card-text"><small class="text-muted">{{permisos.user.email}}</small></p>
                  <button class="btn btn-danger" @click="eliminarPermiso_coordinador(permisos.id)"> Eliminar</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </b-row>
    </b-modal>

    <b-modal id="permisos_analistas" false size="lg" no-close-on-backdrop="true"  title="Permisos a analistas" hide-footer>
      <h4>Regionales</h4>
        <b-row>
          <b-col>
                <v-select v-model="regional_id" @change="listarPermisos_analistas()" :options="regionales" :reduce="regionales => regionales.id" :getOptionLabel="option => option.nombre" :disabled="ver"></v-select>
          </b-col>
          <b-col>
            <button class="btn btn-success btn-sm" @click="listarPermisos_analistas()" :disabled="!regional_id">Buscar</button>
          </b-col>
         </b-row>
         <h4>Analistas</h4>
        <b-row> 
          <b-col>
                <v-select v-model="analista_id" :options="analistas" :reduce="analistas => analistas.id" :getOptionLabel="option => option.nombre+' '+option.apellido" :disabled="ver"></v-select>
          </b-col>
          <b-col>
            <button class="btn btn-success btn-sm" @click="asignarPermisoAnalista(analista_id,regional_id)" :disabled="!analista_id||!regional_id">Asignar</button>
          </b-col>

        </b-row> 
      <hr>
      <h4>Permisos Otorgados Regional</h4>
      <b-row>
        <div class="col-4" v-for="permisos in permisos_analistas" :key="permisos.id">
          <div class="card mb-3">
            <div class="row g-0">
              <div class="col-md-4">
                <img src="" class="img-fluid rounded-start" alt="">
              </div>
              
              <div class="col-md-8">
                <div class="card-body">
                  <h5 class="card-title"> {{permisos.user.nombre}} {{permisos.user.apellido}}</h5>
                 <p class="card-text"><small class="text-muted">{{permisos.user.email}}</small></p>
                  <button class="btn btn-danger" @click="eliminarPermiso_analista(permisos.id)"> Eliminar</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </b-row>
    </b-modal>



    <b-modal id="modal" false size="lg"  title="Gestión de entidades" hide-footer>
          <ValidationObserver ref="form">

            <b-row>
              <b-col>
                <div class="form-group">
                  <label>Nombre  </label>
                  <ValidationProvider name="cargo" rules="required|alpha_spaces" v-slot="{ errors }">
                        <input v-model="form.nombre"  type="text" class="form-control" placeholder=" " :disabled="ver">
                        <span style="color:red">{{ errors[0] }}</span>
                  </ValidationProvider>
                </div>
              </b-col>
                <b-col>
                <div class="form-group">
                  <label>Apellido  </label>
                  <ValidationProvider name="cargo" rules="required|alpha_spaces" v-slot="{ errors }">
                        <input v-model="form.apellido"  type="text" class="form-control" placeholder=" " :disabled="ver">
                        <span style="color:red">{{ errors[0] }}</span>
                  </ValidationProvider>
                </div>
              </b-col>
              </b-row>
              <b-row>
                <b-col>
                    <div class="form-group">
                    <label>Email</label>
                    <ValidationProvider name="descripcion" rules="required|email" v-slot="{ errors }">
                            <input v-model="form.email"  type="text" class="form-control" placeholder=" " :disabled="ver">
                            <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
                <b-col>
                    <div class="form-group">
                    <label>Teléfono</label>
                    <ValidationProvider name="telefono" rules="required" v-slot="{ errors }">
                            <input v-model="form.telefono"  type="text" class="form-control" placeholder=" " :disabled="ver">
                            <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
              </b-row> 
              <hr>
              <b-row>
                <b-col>
                    <div class="form-group">
                    <label>Entidad </label>
                    <ValidationProvider name="empresa" rules="required" v-slot="{ errors }">
                            <input v-model="form.empresa"  type="text" class="form-control" placeholder=" " disabled>
                            <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
                <b-col>
                    <div class="form-group">
                    <label>Página Web</label>
                    <ValidationProvider name="pagina" rules="required" v-slot="{ errors }">
                            <input v-model="form.pagina"  type="text" class="form-control" placeholder=" " :disabled="ver">
                            <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
              </b-row>   
              <b-row>
                <b-col>
                    <div class="form-group">
                    <label>Nit </label>
                    <ValidationProvider name="nit" rules="required" v-slot="{ errors }">
                            <input v-model="form.nit"  type="text" class="form-control" placeholder=" " :disabled="ver">
                            <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
                <b-col>
                    <div class="form-group">
                    <label>Teléfono </label>
                    <ValidationProvider name="telefono_empresa" rules="required" v-slot="{ errors }">
                            <input v-model="form.telefono_empresa"  type="text" class="form-control" placeholder=" " :disabled="ver">
                            <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
              </b-row>   
              <b-row>
                <b-col>
                    <div class="form-group">
                    <label>Dirección </label>
                    <ValidationProvider name="direccion" rules="required" v-slot="{ errors }">
                            <input v-model="form.direccion"  type="text" class="form-control" placeholder=" " :disabled="ver">
                            <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
              </b-row>  
              <hr>
              <b-row>
                <b-col>
                    <div class="form-group">
                    <label>Valor de contrato </label>
                    <ValidationProvider name="valor_Contrato" rules="required" v-slot="{ errors }">
                            <input v-model="form.valor_contrato"  type="text" class="form-control" placeholder=" " :disabled="ver">
                            <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
                <b-col>
                    <div class="form-group">
                    <label>Require presupuesto</label>
                    <ValidationProvider name="requiere_presupuesto" rules="required" v-slot="{ errors }">
                          <select v-model="form.requiere_presupuesto"  name="requiere_presupuesto" class="form-control form-control-lg" >
                              <option value="Si">Si</option>
                              <option value="No">No</option>
                          </select>
                          <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
              </b-row>
              <b-row>
                <b-col>
                    <div class="form-group">
                    <label>Presupuesto </label>
                    <ValidationProvider name="presupuesto" rules="required" v-slot="{ errors }">
                            <input v-model="form.presupuesto"  type="text" class="form-control" placeholder=" " :disabled="ver">
                            <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
                <b-col>
                    <div class="form-group">
                    <label>Aviso</label>
                    <ValidationProvider name="aviso" rules="required" v-slot="{ errors }">
                            <input v-model="form.aviso"  type="text" class="form-control" placeholder=" " :disabled="ver">
                            <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
              </b-row> 
               <b-row>
                <b-col>
                  <label>Logo</label>
                    <div id="preview">
                      <img v-if="url" width="100%" style="float:center!importan" class="rounded"  :src="url" />
                    </div>
                        <b-form-file
                          v-model="file"
                          placeholder="Seleccione su image..."
                          drop-placeholder="Drop file here..."
                          @change="onFileChange"
                      ></b-form-file>
                </b-col>
              </b-row> 
                  
        </ValidationObserver>
        <button class="btn btn-block float-right btn-success" @click="switchLoc" v-if="!ver && !editMode">Guardar</button>
        <button class="btn btn-block float-right btn-success" @click="switchLoc" v-if="!ver && editMode">Editar</button>
     </b-modal>

  </Layout>
</template>

<script>
import "vue-select/dist/vue-select.css";
import vSelect from "vue-select";
import {mapState,mapMutations, mapActions} from 'vuex'
import { ValidationProvider, ValidationObserver } from "vee-validate";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";


/**
 * Dashboard component
 */
export default {
  components: {
    Layout,
    PageHeader,
    ValidationProvider,
    ValidationObserver,
    vSelect
  },
  data() {
    return {
      title: "Administracion",
      items: [
        {
          text: "Gestión corporativa"
        },
        {
          text: "Entidades",
          active: true
        }
      ],
     dropzoneOptions: {
        thumbnailWidth: 150,
        maxFilesize: 0.5,
        headers: { "My-Awesome-Header": "header value" }
      },
      ver:false,
      url:"",
      selected: [
          "Categoria"
      ],
      url_firma:"",
      modal: true,
      file:null,
      firma:null,
      email: "",
      administrador_id: "",
      coordinador_id: "",
      regional_id: "",
      analista_id: "",
      password: "",
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [10, 25, 50, 100],
      filter: null,
      filterOn: [],
      sortBy: "age",
      sortDesc: false,
      fields: ["nombre","apellido","telefono","email","empresa","actions"],
      entidades: [], 
      users: [],
      regionales: [],
      admin: [],
      analistas: [],
      fields_user: ["nombre","apellido","email","actions"],
      permisos: [],
      permisos_admin: [],
      permisos_coordinadores: [],
      permisos_analistas: [],
      fields_permisos: ["Nombre","Email","Entidad","actions"],
      editMode:false,
      form:{
          'id':'',
          'nombre':'Jose',
          'apellido':'Rios',
          'email':'leo@gmail.com',
          'telefono':'456465',
          'empresa':'COLPATRIA',
          'pagina':' www.colpatria.com',
          'nit':'7897998',
          'telefono_empresa':'12456465',
          'direccion':'direccion calle troncal',
          'valor_contrato':'123456464',
          'requiere_presupuesto':'Si',
          'presupuesto':'1845426',
          'aviso':'aviso'
      }
    }
  },
  computed:{
        ...mapState(['counter'])
   },
  methods: {
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }, 
    ...mapActions(['guardarUsuario']),
       switchLoc(){
      if (!this.editMode) {
        this.$refs.form.validate().then(esValido => {
            if (esValido) {
              this.agregarEntidad();
            } else {}
          });        
        }else{
          this.$refs.form.validate().then(esValido => {
          if (esValido) {
            this.editarentidades();
          } else {
        }});
      }
    },
   async agregarEntidad(){
     let data = new FormData();
      var formulario = this.form;
        for ( var key in formulario) {
            if (key=='categorias') {
                data.append(key,JSON.stringify(formulario[key]));
            }else{
                data.append(key,formulario[key]);
            }
        }
       if (this.file) {
        data.append('filename',this.file);
       }
       await this.axios.post('api/entidades', data, {
           headers: {
            'Content-Type': 'multipart/form-data'
           }}).then(response => {
            if (response.status==200) {
               this.$swal(
                   'Agregado exito!',
                    '',
                    'success');
               this.listarentidades();
               this.$root.$emit("bv::hide::modal", "modal", "#btnShow");
               ///limpiar el formulario
                for (var key in formulario) {
                   this.form[key]="";
                 }
              }
            }).catch(e => {
              console.log(e.response.data.menssage);
              this.$swal(e.response.data);
          });
      },
    async editarentidades(){
     let data = new FormData();
       var formulario = this.form;
        for ( var key in formulario) {
            if (key=='categorias') {
                 data.append(key,JSON.stringify(formulario[key]));
            }else{
                data.append(key,formulario[key]);
            }
        }
        if (this.file) {
            data.append('filename',this.file);
         }
        await this.axios.put('api/entidades', data).then(response => {
            if (response.status==200) {
                this.$swal(
                    'Editado con exito!',
                    '',
                    'success'
                );
               this.listarentidades();
               this.$root.$emit("bv::hide::modal", "modal", "#btnShow");
               ///limpiar el formulario
                for (var key in formulario) {
                   this.form[key]="";
                 }
              }
            }).catch(e => {
              console.log(e.response.data.menssage);
                this.$swal(
                    'Error de peticion!',
                    '',
                    'warning'
                );
            });
     },
     async eliminarentidades(id){
        let data = new FormData();
        data.append('id',id);
        await this.axios.post('api/entidades/delete',data, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }}).then(response => {
              if (response.status==200) {
                this.$swal(
                    'Eliminado con exito!',
                      '',
                      'success'
                );
                this.listarentidades();
                }
              }).catch(e => {
                console.log(e.response.data.menssage);
                this.$swal(e.response.data);
          });
      }, 
       
      asignarPermiso(id){
        for (let index = 0; index < this.permisos_admin.length; index++) {
          if (this.permisos_admin[index].uid===id) {
              this.$swal(
                  'Usuario ya tiene permiso para esta entidad!',
                  '',
                  'success');
                  return;
          }
        }
        this.$swal({
          title: 'Desea asignar el  permiso?',
          icon: 'question',
          iconHtml: '',
          confirmButtonText: 'Si',
          cancelButtonText: 'No',
          showCancelButton: true,
          showCloseButton: true
        }).then((result) => {
          if (result.isConfirmed) {
            this.asignarPermisos(id);
          }
        })
      },
      asignarPermisoAnalista(id,regional){
        for (let index = 0; index < this.permisos_analistas.length; index++) {
          if (this.permisos_analistas[index].uid===id) {
              this.$swal(
                  'Usuario ya tiene permiso para esta regional en esta en esta entidad!',
                  '',
                  'success');
                  return;
          }
        }
        this.$swal({
          title: 'Desea asignar el  permiso?',
          icon: 'question',
          iconHtml: '',
          confirmButtonText: 'Si',
          cancelButtonText: 'No',
          showCancelButton: true,
          showCloseButton: true
        }).then((result) => {
          if (result.isConfirmed) {
            this.asignarPermiso_analistas(id,regional);
          }
        })
      },
      async asignarPermisos(id){
        let data = new FormData();
        data.append('eid',this.form.id);
        data.append('uid',id);

        await this.axios.post('api/permisos/administradores',data, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }}).then(response => {
              if (response.status==200) {
                this.$swal(
                      'Asignado con exito!',
                      '',
                      'success'
                );
                this.listarPermisos(this.form.id);
                this.listarUsers();
                }
              }).catch(e => {
                console.log(e.response.data.menssage);
                this.$swal(e.response.data);
          });
      }, 
      eliminarPermiso(id){
        this.$swal({
          title: 'Desea borrar este permiso?',
          icon: 'question',
          iconHtml: '',
          confirmButtonText: 'Si',
          cancelButtonText: 'No',
          showCancelButton: true,
          showCloseButton: true
        }).then((result) => {
          if (result.isConfirmed) {
            this.eliminarPermisos(id);
          }
        })
      },
      async eliminarPermisos(id){
        let data = new FormData();
        data.append('id',id);
        await this.axios.post('api/permisos/administradores/delete',data, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }}).then(response => {
              if (response.status==200) {
                this.$swal(
                    'Eliminado con exito!',
                      '',
                      'success'
                );
                this.listarPermisos(this.form.id);
                }
              }).catch(e => {
                console.log(e.response.data.menssage);
                this.$swal(e.response.data);
          });
      }, 
      async eliminarPermisos_coordinador(id){
        let data = new FormData();
        data.append('id',id);
        await this.axios.post('api/permisos/coordinadores/delete',data, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }}).then(response => {
              if (response.status==200) {
                this.$swal(
                    'Eliminado con exito!',
                      '',
                      'success'
                );
                this.listarPermisos_coordinadores(this.form.id);
                }
              }).catch(e => {
                console.log(e.response.data.menssage);
